@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&display=swap);
body {
  margin: 0;
  font-family: 'Lato', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Lato', sans-serif;
}
.Footer {
    position: absolute;
    width: 100%;
    height: 40vh;
    background-color: #FCC700;
    border-top: 3vh solid #000000
}

.Footer__content {
    height: 100%;
    margin-left: 10%;
    margin-right: 10%;
    justify-content: space-between;
    display: flex;

}

.Footer__contener {
    padding: 10px 10px 0 10px;
    width: 20%;
}

.Footer__contener h2 {
    font-size: 3.5vh;
}

.Footer__contener h3 {

    font-size: 2vh;
    margin-top: 3vh;
    margin-bottom: 0.5vh;
}

.Footer__contener h4 {
    display: inline;
    margin: 0;
    margin-right: 10px;
}

.Footer__contener p {
    font-size: 2vh;
    margin: 0
}

.Footer__map {
    border: 0;
    margin-top: 3vh;
    height: 60%;
    width: 100%;
    box-shadow: 10px 10px 0px -3px rgba(0, 0, 0);
}
.Menu {
    position: fixed;
    height: 10vh;
    width: 100vw;
    background-color: rgb(0, 0, 0);
    color: #E5E5E5;
    display: flex;
    z-index: 2;
}

.Menu__padding {
    height: 10vh;
    width: 100vw;
}

.Menu__logo {
    position: absolute;
    left: 2%;
    top: 50%;
    transform: translateY(-50%);
}

.Menu__links {
    position: absolute;
    right: 10%;
    height: 100%;
    width: 60%;
    display: flex;
    justify-content: flex-end;
}

.link {
    font-size: 2vh;
    font-weight: 400;
    line-height: 10vh;
    width: 10vw;
    text-align: center;

}

.link::before {
    content: "";
    width: 100%;
    height: 100%;
    background-color: pink;
    z-index: 5;
}
.Slider {
    position: relative;
    height: 45vh;
    width: 100%;
    background-size: 100% 100%;
    border-top: solid 3vh #FCC700;
    border-bottom: solid 3vh #FCC700;
}

.Slider__box {
    position: absolute;
    top: 25%;
    left: 20%;
    width: 25%;
    height: 40%;
    background-color: #E5E5E5;
    opacity: 0.9;
}

.Slider__box h2 {
    font-size: 4vh;
    margin: 10px;
    color: #14213D;
}

.Slider__box p {
    font-size: 2vh;
    margin: 10px;
    opacity: 1;
}

.Slider__Arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    opacity: 0.7;
    cursor: pointer;
    transition: all 0.5s ease-in-out;
}

.Slider__Arrow:hover {
    opacity: 1;
}

.prevArrow {
    left: 0;
}

.nextArrow {
    right: 0;
}
.aboutColumn {
    width: 25%;
    height: 60%;
    text-align: center;
    border-bottom: 3px solid black;
}

.aboutColumn__icon {
    position: absolute;
    width: 4%;
    margin-left: -2%;
    margin-top: -2%;
    transform: rotate(-25deg);
    transition: all 0.5s ease-in-out;
}

.aboutColumn__icon:hover {
    transform: rotate(0deg);
}


.aboutColumn__title {
    width: 100%;
    display: inline-block;
    background-color: #FCC700;
    height: 6vh;
    z-index: -1;
    box-shadow: 10px 10px 0px -3px rgba(0, 0, 0);
}

.aboutColumn__title::before {
    position: absolute;
    content: "";
    height: 8vh;
    width: 6vw;
    z-index: 0;
}

.aboutColumn__title h2 {
    padding-top: 1.2vh;
    margin-top: 0;
    font-size: 3vh;
    text-align: center;
}

.aboutColumn__description {
    display: inline-block;
    width: 90%;
    margin-top: 0.5vh;
    text-align: inherit;
}

.aboutColumn__description p {
    padding: 20px 20px 20px 20px;
    font-size: 2vh;
    margin: 0;
    color: #000000
}
.shopColumn {
    width: 40%;
    height: 30vh;
    border-radius: 15px;
    border: 2px solid black;
}

.shopColumn__title {

}

.mainPage {
    background-color: #E5E5E5;
}

.mainPage__shops {
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: space-between;
    width: 40%;
}

.mainPage__shop{

}

.mainPage__aboutTitle {
    font-size: 4vh;
    text-align: center;
}

.mainPage__aboutTitle h2 {
    margin: 0;
    margin-top: 4vh;
}

.mainPage__About {
    margin-left: auto;
    margin-right: auto;
    padding-top: 10vh;
    padding-bottom: 10vh;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 70%;
}
